import React from 'react';
import { Link } from 'gatsby-plugin-intl';
import { useStaticQuery, graphql } from 'gatsby';
import Img from 'gatsby-image';
import { AllorsUnifiedGood, Query } from '../../graphql-types';
import Card from 'react-bootstrap/Card';

const Product: React.FunctionComponent<{ product: AllorsUnifiedGood }> = ({ product }) => {
  const fluid = product.primaryPhoto ? product.primaryPhoto.file.childImageSharp.fluid : undefined;

  const {
    allFile: { nodes },
  } = useStaticQuery<Query>(
    graphql`
      {
        allFile(filter: { name: { in: ["no-image"] } }) {
          nodes {
            name
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    `,
  );

  const noImage = nodes[0].childImageSharp.fluid;

  return (
    <div className="col-md-4 col-sm-6">
      <Link to={`/${product.slug}`} className="product-link">
        <Card>
          {fluid ? <Img fluid={fluid} /> : <Img fluid={noImage} />}
          <Card.Body>
            <Card.Title className="text-center">{product.name}</Card.Title>
          </Card.Body>
        </Card>
      </Link>
    </div>
  );
};

export default Product;
