import React from 'react';
import { graphql } from 'gatsby';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FormattedMessage, Link } from 'gatsby-plugin-intl';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import Product from '../components/product';
import { Query, AllorsUnifiedGood } from '../../graphql-types';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { BreadCrumbs } from '../components/breadcrumbs';
import { Separator } from '../components/separator';

const FamilyPage: React.FunctionComponent<{ data: Query }> = ({ data: { allorsProductCategory } }) => {
  const family = allorsProductCategory;
  const seoImage = family.categoryImage?.file?.childImageSharp?.original?.src;
  const groups = [...(family._children || [])].sort((a, b) => a.name && b.name && a.name.localeCompare(b.name));

  let products: AllorsUnifiedGood[];
  if (family.allProducts != null) {
    products = [...(family.allProducts?.map((v) => v.UnifiedGood) || [])].sort((a, b) => a.name && b.name && a.name.localeCompare(b.name));
  }

  return (
    <Layout>
      <SEO title={family.name} description={family.seoDescription ?? family.description} image={seoImage} />

      <main role="main">
        <article>
          <header className="page-header">
            <Container>
              <Row>
                <Col>
                  <BreadCrumbs section="catalogue" family={family} />
                  <h1 className="page-title">{family.name}</h1>
                  <p className="page-description">{family.seoDescription}</p>
                  <Separator />
                </Col>
              </Row>
            </Container>
          </header>
          <Container>
            <Row>
              <Col xl="3" lg="4" xs="12">
                <div className="list-group mb-4">
                  <Link to={'/catalogue'} className="list-group-item list-group-item-action active">
                    <FontAwesomeIcon icon="chevron-left" /> <FormattedMessage id="back" />
                  </Link>
                  {groups &&
                    groups.map((category) => {
                      return (
                        <Link key={`/${category.slug}`} to={'/' + category.slug} className="list-group-item list-group-item-action">
                          {category.name}
                        </Link>
                      );
                    })}
                </div>
              </Col>
              <Col xl="9" lg="8" xs="12">
                <Row>
                  {products &&
                    products.map((product) => {
                      return <Product key={product.slug} product={product}></Product>;
                    })}
                </Row>
              </Col>
            </Row>
          </Container>
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    allorsProductCategory(slug: { eq: $slug }) {
      slug
      name
      seoDescription
      categoryImage {
        file {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      _children {
        slug
        name
        categoryImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
      allProducts {
        UnifiedGood {
          slug
          name
          primaryPhoto {
            file {
              childImageSharp {
                id
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
    }
  }
`;

export default FamilyPage;
